.tcloud,
.tcloud:before,
.tcloud:after {
  border-radius: 35px;
  background: rgb(145, 133, 132);
}

.tcloud {
  display: inline-block;
  vertical-align: top;
  width: 100px;
  height: 30px;
  border-radius: 50px 35px 35px 15px;
  position: relative;
}

.tcloud:before,
.tcloud:after {
  content: "";
  display: block;
  position: absolute;
}

.tcloud:before {
  width: 40px;
  height: 45px;
  left: 12px;
  bottom: 0;
}

.tcloud:after {
  width: 40px;
  height: 50px;
  right: 16px;
  bottom: 0;
}

.tcloud:nth-child(1) {
  position: absolute;
  top: 180px;
  left: -450px;
}

.tcloud:nth-child(2) {
  position: absolute;
  top: 80px;
  left: -230px;
}

.tcloud:nth-child(4) {
  position: absolute;
  top: 50px;
  left: 230px;
}

.tcloud:nth-child(5) {
  position: absolute;
  top: 140px;
  right: 100px;
}

.tcloud {
  -webkit-animation: cloudmove 50s linear infinite backwards;
          animation: cloudmove 50s linear infinite backwards;
}

@-webkit-keyframes cloudmove {
  0% {
    transform: translateX(-450px);
  }
  100% {
    transform: translateX(1090px);
  }
}

@keyframes cloudmove {
  0% {
    transform: translateX(-450px);
  }
  100% {
    transform: translateX(1090px);
  }
}

.tstars {
  width: 100%;
}

.tstar {
  display: inline-block;
  position: relative;
  -webkit-animation: twinkle 7s -1s infinite;
          animation: twinkle 7s -1s infinite;
}

.tstar:nth-child(even) {
  -webkit-animation: twinkle 7s 2s infinite;
          animation: twinkle 7s 2s infinite;
}

@-webkit-keyframes twinkle {
  0% {
    opacity: 1;
  }
  60% {
    opacity: 0.2;
  }
}

@keyframes twinkle {
  0% {
    opacity: 1;
  }
  60% {
    opacity: 0.2;
  }
}

.tstar:before,
.tstar:after {
  content: "";
  display: block;
  width: 3px;
  height: 10px;
  background: #b0a69b;
  border-radius: 45%;
}

.tstar:before {
  transform: rotate(-45deg);
  position: absolute;
  top: 0;
}

.tstar:after {
  transform: rotate(45deg);
}

.tstar:nth-child(1) {
  transform: translate(60px, -210px);
}

.tstar:nth-child(2) {
  transform: translate(180px, -110px);
}

.tstar:nth-child(3) {
  transform: translate(210px, -215px);
}

.tstar:nth-child(4) {
  transform: translate(370px, -15px);
}

.tstar:nth-child(5) {
  transform: translate(440px, 50px);
}

.tstar:nth-child(6) {
  transform: translate(440px, -190px);
}

.tstar:nth-child(7) {
  transform: translate(-5px, 20px);
}

.mountain {
